import React, { ComponentType, createContext, FunctionComponent, useContext, useMemo, useState } from 'react';
import { Omit } from 'Types/Omit';

export interface AppStoreContext {
    something: number;
    setSomething: (hello: number) => void;
}

const Context = createContext<AppStoreContext>(undefined!);

/* as a HOOK
 *  test: wrap the component in the text with <TestAppStoreProvider ...propsYouMightWantToOverride><YourComponent></AppStoreProvider>
 *  */
function useAppStore() {
    return useContext(Context);
}

/* as a HIGHER ORDER COMPONENT
 *  test: export the component without the connect (underscore it, so it's clear it's not meant to external use)
 *  useAppStore is a cleaner code in the component file I would say, and in test 1 wrap isn't that bad. so preference is useAppStore :)
 *  */
const withAppStore = <P extends AppStoreContext, PWithoutAppStoreContext = Omit<P, keyof AppStoreContext>>(
    Component: ComponentType<P>
): FunctionComponent<PWithoutAppStoreContext> => {
    function fn(props) {
        const config: AppStoreContext = useAppStore();
        // https://github.com/Microsoft/TypeScript/issues/28938
        // @ts-ignore
        return <Component {...config} {...props} />;
    }
    fn.displayName = `withAppStore:${Component.displayName || Component.name || 'NoNameComponent'}`;

    return fn;
};

const AppStoreProvider: FunctionComponent<{}> = props => {
    /* 'something' is an example here to make the context changeable AND have memorized context, this way:
     * - no unnecessary rerender
     * - having props for context AND settable properties
     **/
    const [something, setSomething] = useState(0);

    // memorized variable. if props and something don't change, same value is returned so no rerender trigger.
    const context = useMemo<AppStoreContext>(() => {
        return {
            something,
            setSomething,
        };
    }, [props, something]);

    return <Context.Provider value={context}>{props.children}</Context.Provider>;
};

export { AppStoreProvider, useAppStore, withAppStore };
