import React, { FunctionComponent } from 'react';
import ClassNames from './PageRoot.module.scss';
import classNames from 'classnames';
import { CmsPageMetaContext } from 'Types/CmsPageMetaContext';
import SEO from '../../../seo';
import Navigation from '../../../Navigation/Navigation';
import { PageConfigProvider } from '../../../../contexts/PageConfig/PageConfig';

export type PageRootProps = {
    withNavigation?: boolean;
} & CmsPageMetaContext;

const PageRoot: FunctionComponent<PageRootProps> = ({
    children,
    metaKeywords,
    metaDescription,
    metaTitle,
    locales,
    currentLocaleCode,
    slug,
    pageLinkMap,
    withNavigation = true,
}) => {
    return (
        <PageConfigProvider
            locales={locales}
            currentLocaleCode={currentLocaleCode}
            slug={slug}
            pageLinkMap={pageLinkMap}
        >
            <div className={classNames(ClassNames.Page, !withNavigation && ClassNames.noNav)}>
                <SEO title={metaTitle} description={metaDescription} keywords={metaKeywords} />
                {withNavigation && <Navigation />}
                {children}
            </div>
        </PageConfigProvider>
    );
};

export default PageRoot;
