import React, { ComponentType, createContext, FunctionComponent, useContext, useMemo } from 'react';
import PageLinkMap from 'Types/PageLinkMap';
import { Locale } from 'Types/Locale';
import { Omit } from 'Types/Omit';

export interface PageConfigContext {
    locales: Locale[];
    currentLocaleCode: string;
    slug: string;
    pageLinkMap: PageLinkMap;
}

export interface PageConfigProps {
    locales: Locale[];
    currentLocaleCode: string;
    slug: string;
    pageLinkMap: PageLinkMap;
}

const Context = createContext<PageConfigContext>(undefined!);

/* as a HOOK
 *  test: wrap the component in the text with <TestPageConfigProvider ...propsYouMightWantToOverride><YourComponent></TextPageConfigProvider>
 *  */
function usePageConfig() {
    return useContext(Context);
}

/* as a HIGHER ORDER COMPONENT
 *  test: export the component without the connect (underscore it, so it's clear it's not meant to external use)
 *  usePageConfig is a cleaner code in the component file I would say, and in test 1 wrap isn't that bad. so preference is usePageConfig :)
 *  */
const withPageConfig = <P extends PageConfigContext, PWithoutPageConfigContext = Omit<P, keyof PageConfigContext>>(
    Component: ComponentType<P>
): FunctionComponent<PWithoutPageConfigContext> => {
    function fn(props) {
        const config: PageConfigContext = usePageConfig();
        // https://github.com/Microsoft/TypeScript/issues/28938
        // @ts-ignore
        return <Component {...config} {...props} />;
    }
    fn.displayName = `withPageConfig:${Component.displayName || Component.name || 'NoNameComponent'}`;

    return fn;
};

const PageConfigProvider: FunctionComponent<PageConfigProps> = props => {
    // memorized variable. if props don't change, same value is returned so no rerender trigger.
    const context = useMemo<PageConfigContext>(() => {
        return {
            locales: props.locales,
            currentLocaleCode: props.currentLocaleCode,
            slug: props.slug,
            pageLinkMap: props.pageLinkMap,
        };
    }, [props]);

    return <Context.Provider value={context}>{props.children}</Context.Provider>;
};

export { PageConfigProvider, usePageConfig, withPageConfig };
