import React, { ReactNode } from 'react';
import PageRoot from '../../cms/pages/PageRoot/PageRoot';
import { CmsPageContext } from 'Types/CmsPageContext';

interface FunctionDeclaration {
    (
        apiCallbackContext: { element: ReactNode; props: { pageContext: CmsPageContext } },
        options: Record<string, any>
    ): ReactNode;
}

const WrapPageElement: FunctionDeclaration = ({ element, props }) => {
    return (
        <PageRoot {...props.pageContext.meta} withNavigation={props.pageContext.withNavigation}>
            {element}
        </PageRoot>
    );
};

export default WrapPageElement;
